.overly {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(121, 121, 121, 0.2);
  backdrop-filter: blur(2px);
  display: flex;
  justify-content: center;
  align-items: center;
  opacity: 0;
  transition: 0.3s;
  color: white;
  cursor: pointer;
  padding: 10px;
  text-transform: uppercase;
}
.overly h3 {
  font-size: 20px;
  font-weight: 500;
  letter-spacing: 3px;
  text-align: center;
  margin: 0;
}

.image-holder:hover .overly {
  opacity: 1;
}

.images-container {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  /* margin-left: -20px; gutter size offset */
  width: auto;
  gap: 10px;
  padding-top: 20px;
}

.images-container-column {
  background-clip: padding-box;
  gap: 10px;
}

/* .images-container-column:not(:first-child) {
  padding-left: 20px; /* gutter size 
} */

.images-container .image-holder {
  height: 300px;
  background-position: center;
  background-size: contain;
  background-repeat: no-repeat;
  width: auto;
}

.images-container .image-holder {
  margin-bottom: 20px;
  display: flex;
  justify-content: center;
  align-items: center;
  position: relative;
  transition: 0.3s;
}

.images-container .image-holder img {
  object-fit: cover;
  width: 100%;
  margin: 0;
  padding: 0;
}

@media (max-width: 610px) {
  .images-container {
    padding: 0 !important ;
    margin-left: -10px;
  }
  .images-container-column:not(:first-child) {
    padding-left: 10px;
  }
  .images-container-column .image-holder {
    margin-bottom: 10px;
  }
}

.images-wraper {
  min-height: 200px;
}

.load-images {
  display: flex;
  justify-content: center;
  align-items: flex-end;
  margin: 50px auto;
  height: 35px;
}
.load-images div {
  width: 30px;
  height: 30px;
  background-color: var(--secondry-color);
  border-radius: 50%;
  margin: 0 5px;
  animation-name: up-and-down;
  animation-duration: 0.7s;
  animation-timing-function: ease;
  animation-iteration-count: infinite;
  animation-direction: alternate;
}
.load-images .two {
  animation-delay: 0.3s;
}
.load-images .three {
  animation-delay: 0.6s;
}

@keyframes up-and-down {
  to {
    opacity: 0.2;
    transform: translateY(-5px);
  }
}

.no-posts-wraper {
  margin-top: 70px;
  height: calc(100vh - 140px);
  display: flex;
  justify-content: center;
  align-items: center;
}

.no-posts {
  font-size: 40px;
  font-weight: 800;
  color: #bbb;
}
