.login-cont {
  width: 100%;
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: #9ea6e5;
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
  color: #fff;
}

.my-form {
  width: 100%;
  max-width: 400px;
  border-radius: 30px;
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 40px;
  padding: 30px;
  margin: -60px 0 0;
}

.my-form h1 {
  font-size: 50px;
  font-weight: 500;
  color: var(--main-color-dark);
  margin: 0;
  text-transform: capitalize;
}

.my-form form {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  gap: 25px;
  width: 100%;
  max-width: 280px;
}

.my-form form input {
  width: 100%;
  height: 40px;
  border-radius: 5px;
  background-color: transparent;
  color: var(--main-color-dark);
  font-size: 16px;
  padding: 10px 10px 10px 40px;
  border: none;
  border-bottom: 3px solid var(--main-color-dark);
}

.my-form form input:focus {
  outline: none;
  border-bottom: 3px solid var(--secondry-color);
}
.my-form form input::placeholder {
  color: #333;
}
.my-form form input:focus-visible {
  outline: none;
  border-bottom: 3px solid var(--secondry-color);
}

.my-form form button {
  border-radius: 5px;
  border: none;
  background-color: #fff;
  padding: 12px;
  width: 100%;
  cursor: pointer;
  color: #9ea6e5;
  font-size: 14px;
  transition: 0.3s;
  font-weight: 500;
  margin-top: 20px;
}

.my-form form button:hover {
  opacity: 0.9;
}

.my-form form .my-link {
  color: var(--main-color-dark);
  font-size: 14px;
}

.my-form form .my-link a {
  color: var(--secondry-color);
  text-decoration: none;
  font-weight: 500;
}
.my-form form .my-link a:hover {
  text-decoration: underline;
}

.entry-input-wraper {
  position: relative;
  width: 100%;
  border-bottom: 1px solid #fff;
}

.entry-input-wraper .entry-icon {
  position: absolute;
  top: 50%;
  left: 10px;
  transform: translateY(-50%);
  color: var(--main-color-dark);
  font-size: 20px;
  cursor: pointer;
}
